import helper from '../../common/common.js';
export default {
	data() {
		return {
			isactive:false,
			isinfo:1,
			username:'',
			headurl:'',
			autograph:'',
			user_id:''
		}
	},
	onPageScroll(Object){
		if(Object.scrollTop > 0){
			this.isactive = true
		}else if(Object.scrollTop == 0){
			this.isactive = false
		}
	},
	onLoad(){
		this.getRegisterID()
	},
	onShow(){
		
		uni.getStorage({
			key:'userinfo',
			success:(res)=>{
				this.isinfo = 2;
				this.username = res.data.username;
				this.headurl =  helper.websiteUrl+res.data.headurl;
				this.autograph = res.data.autograph;
				this.user_id = res.data.user_id;
			}
		})
		plus.screen.lockOrientation("portrait-primary");
	},
	onUnload(){
		plus.screen.lockOrientation("portrait-primary");
	},
	methods: {
		test_jg(){
			
			const jyJPush = uni.requireNativePlugin('JY-JPush');
			jyJPush.setJYJPushAlias({
				userAlias: 'testAlias'
				}, result=> {
					console.log(result)
				//  设置成功或者失败，都会通过这个result回调返回数据；数据格式保持极光返回的安卓/iOS数据一致
				//  注：若没有返回任何数据，考虑是否初始化完成
				uni.showToast({
					icon:'none',
					title: JSON.stringify(result)
				})
			});
		},
		getRegisterID() {
			console.log('获取RegisterID');
			const jyJPush = uni.requireNativePlugin('JY-JPush');
			jyJPush.getRegistrationID(result => {
				console.log(result)
				uni.showToast({
					icon: 'none',
					title: JSON.stringify(result)
				})
			});
		},
		question(){
			uni.navigateTo({
			    url: '../problemfeedback/problemfeedback'
			});
		},
		follow(){
			uni.navigateTo({
			    url: '../myfollow/myfollow'
			});
		},
		collect(){
			uni.navigateTo({
			    url: '../mycollect/mycollect'
			});
		},
		coupon(){
			uni.navigateTo({
			    url: '../mycoupon/mycoupon'
			});
		},
		order(){
			uni.navigateTo({
			    url: '../myorder/myorder'
			});
		},
		address(){
			uni.navigateTo({
			    url: '../receivingaddress/receivingaddress'
			});
		},
		appointment(){
			uni.navigateTo({
			    url: '../myappointment/myappointment'
			});
		},
		myhousetype(){
			uni.navigateTo({
			    url: '../myhousetype/myhousetype'
			});
		},
		myplan(){
			uni.navigateTo({
			    url: '../myplan/myplan'
			});
		},
		personaldata(){
			if (this.isinfo==1) {
				uni.navigateTo({
				    url: '../login/login'
				});
			} else{
				uni.navigateTo({
				    url: '../personaldata/personaldata'
				});
			}
			
		},
		sub(){
			// uni.navigateTo({
			// 	url: '../designeffect/designeffect'
			// });
			uni.navigateTo({
				url: '../settlement/settlement'
			});
		},
		chat(){
			uni.navigateTo({
				url: '../chat/chat'
			});
		},
		setup(){
			uni.navigateTo({
				url: '../setup/setup'
			});
		},
		contactus(){
			uni.navigateTo({
				url: '../contactus/contactus'
			});
		},
		go_newwin(url){
			console.log(url)
			uni.getStorage({
				key:'userinfo',
				success:(res)=>{
					console.log(res)
					uni.navigateTo({
						url: url
					});
				},fail() {
					uni.navigateTo({
						url: '../login/login'
					});
				}
			})
		},
	}
}
